#layout {
  border-top: 3px solid #00796B;
  box-sizing: border-box;
  margin: 0;
  padding: 60px 30%;
}
#layout > h1 {
  box-sizing: border-box;
  padding: 15px;
  margin: 0;
  text-align: center;
}
#layout > p {
  text-align: center;
}
#layout > p > span {
  box-sizing: border-box;
  padding: 3px 5px;
  margin: 0 5px;
  background-color: rgba(0, 0, 0, 0.1);
}
#layout .logo {
  width: 300px;
  height: 300px;
  margin: auto;
  background-image: url(/logo.svg);
  background-repeat: no-repeat;
  background-size: 100%;
}
